import { DashboardOutlined, UserOutlined, BankOutlined, ApartmentOutlined, CalendarOutlined } from '@ant-design/icons';
import Dashboard from '../pages/Dashboard';
import Setting from "../pages/Setting"
import Service from '../pages/Service';
import WorkerManagement from '../pages/management/workerManagement';
import UserManagement from '../pages/management/userManagement';
import JobList from '../pages/JobList';


const routes = [
    { title: 'Dashboard', icon: DashboardOutlined, url: '/', component: Dashboard },
    { title: 'Service', icon: ApartmentOutlined, url: '/Service', component: Service },
    { title: 'JobList', icon: ApartmentOutlined, url: '/JobList', component: JobList },
    {
        title: 'Management', icon: BankOutlined, url: '/management',  children: [
            { title: 'User', icon: ApartmentOutlined, url: '/', component: UserManagement },
            { title: 'Worker', icon: ApartmentOutlined, url: '/Worker', component: WorkerManagement },
        ]
    },

    { title: 'Settings', icon: CalendarOutlined, url: '/Setting', component: Setting },

].filter(v => (!v.hidden) || v.children?.length);

export default routes;

