import React, { useState } from "react";
import { Card, Layout } from "antd";
import { Routes, Route } from "react-router-dom";
import Menu from "./Menu";
import routes from "./route";
import Breadcrumb from "./Breadcrumb";
import Error404 from "../pages/errors/Error404";
import Top from "./Top";


const newRoutes = [];
function convertMultiToSingle(arr, prevBaseUrl = '') {
    arr.forEach(v => {
        if (v.children) {
            convertMultiToSingle(v.children, prevBaseUrl + v.url);
        } else {
            newRoutes.push({ ...v, url: prevBaseUrl + v.url })
        }
    })
}
convertMultiToSingle([...routes]);


export default function MyLayout() {
    const [collapsed, setCollapsed] = useState(false);

    return (
        <>
            <Layout style={{ minHeight: '100vh' }}>
                <Layout.Sider
                    width={250}
                    collapsible collapsed={collapsed} onCollapse={(value) => setCollapsed(value)}
                    style={{
                        overflow: 'auto',
                        height: '100vh',
                        position: 'fixed',
                        left: 0,
                        top: 0,
                        bottom: 0,
                        maxWidth: '300px!important',
                        width: '300px!important'
                    }}
                >
                    <div className="logo" />
                    <Menu />
                </Layout.Sider>routes
                <Layout className="site-layout">
                    <Top />

                    <Layout.Content
                        style={{
                            margin: '24px 16px 0 240px',
                            overflow: 'initial',
                            marginLeft: !collapsed ? '230px' : '60px'
                        }}
                    >
                        <Breadcrumb />
                        <Card bordered>
                            <Routes>
                                {newRoutes.map((v) => <Route path={v.url} key={v.url} element={<v.component />} />)}
                                <Route path={'*'} element={<Error404 />} />
                            </Routes>
                        </Card>
                    </Layout.Content>
                </Layout>
            </Layout>
        </>
    );

}